<template>
	<div class="RFInvMove">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>库存移动</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div>
				<span>自库位</span>
				<input ref="Loc" placeholder="请输入内容"    @change="toUppercase('Loc')"  @keyup.enter="getFocus('ToLoc')" v-model="Loc" @focus="g_focus('Loc')"></input>
				<i v-if="Loc" @click="clearInput('Loc')" class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>自托盘号</span>
				<input placeholder="请输入内容"    @change="toUppercase('PID')"   @keyup.enter="getFocus('ToLoc')"  v-model="PID" @focus="g_focus('PID')" @blur="g_blur()"></input>
				<i v-if="PID" @click="clearInput('PID')" class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>商品代码</span>
				<input placeholder="请输入内容"    @change="toUppercase('Sku')"   	@keyup.enter="getFocus('ToLoc')"  v-model="Sku" @focus="g_focus('Sku')"></input>
				<i v-if="Sku" @click="clearInput('Sku')" class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>至库位</span>
				<input ref="ToLoc" placeholder="请输入内容"    @change="toUppercase('ToLoc')"   @keyup.enter="getFocus('Qty')" v-model="ToLoc" @focus="g_focus('ToLoc')"></input>
				<i v-if="ToLoc" @click="clearInput('ToLoc')" class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>至托盘号</span>
				<input  placeholder="请输入内容" v-model="ToPID"    @change="toUppercase('ToPID')" @keyup.enter="getFocus('Qty')"  @focus="g_focus('ToPID')"></input>
				<i v-if="ToPID" @click="clearInput('ToPID')" class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>移动数量</span>
				<input    @focus="getSelect('Qty')"  type="number"   @change="toUppercase('Qty')"   ref="Qty" placeholder="请输入内容" @keyup.enter="MoveInventory" v-model="Qty"></input>
				<i v-if="Qty" @click="clearInput('Qty')" class="iconfont icon-cuowu"></i>
			</div>
			<div class="prompt">
				<span>0 - 表示全部可用数量</span>
			</div>
			<div class="g_doubleBtn">
				<div @click="MoveInventory">执行移动</div>
				<div @click="viewInventory">查看库存</div>
			</div>
		</div>
		
		<div v-if="tipsHide"  @touchmove.prevent class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p @ref="inputFocus" @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
		
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"InventoryQuery",
		data(){
			return{
				Loc:"",//6010110
				PID:"",//P1804260001
				Sku:"",//1010001000
				ToLoc:"",//6010120
				ToPID:"",//P1805150002
				Qty:'0',
				listData:{},
				tipsHide:false,//提示信息是否显示
				msg:'',//
				inputFocus:"",
			}
		},
		created(){

			if(this.$store.state.RFInvMoveCondition){

				var RFInvMoveCondition = this.$store.state.RFInvMoveCondition;

				this.Loc = RFInvMoveCondition.Loc;
				this.PID = RFInvMoveCondition.PID;
				this.Sku = RFInvMoveCondition.Sku;
				this.ToLoc = RFInvMoveCondition.ToLoc;
				this.ToPID = RFInvMoveCondition.ToPID;
				this.Qty = RFInvMoveCondition.Qty;
			}
		},
		mounted() {

			this.$refs.Loc.focus();
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
		},
		methods:{

		// 接收iOS/android值
		QRScanAjax:function (qrcode){

			var inputName = this.$store.state.ScanQRCodeInputName;
			
			this[inputName] = qrcode;

			if(inputName == "Loc" || inputName == "PID" || inputName == "Sku") {

				this.$refs.ToLoc.focus();

			}else if(inputName == "ToLoc"  || inputName == "ToPID" ) {

				this.$refs.Qty.focus();

			}else if(inputName == "Qty"){

				this.MoveInventory();
			}
		},

		// 清空数据
		clearData:function(){

			this.Loc = "";
			this.PID = "";
			this.Sku = "";
			this.ToLoc = "";
			this.ToPID = "";
			this.Qty = "";
		},
		focus(e) {
			event.currentTarget.select();
		},
 		// 移动库存
 		MoveInventory:function(){

 			var that = this;

 			if(!this.Loc || !this.ToLoc) {

 				that.msg = "自库位、至库位不能为空";
				that.tipsShow("Loc");
				that.inputFocus = "Loc";
 			}else{

 				if(this.Qty < 0) {

 					that.msg = "移动数量必须大于或等于0";
					that.tipsShow("Qty");
					that.inputFocus = "Qty";
 				}else{

 					var currentUrl = this.HOST + "RFInvMove.do";

 					var postData = {
 						"Loc":this.Loc,
 						"Sku":this.Sku,
 						"PID":this.PID,
 						"ToLoc":this.ToLoc,
 						"ToPID":this.ToPID,
 						"Qty":this.Qty,
 					}

 					this.httpRequest(currentUrl,postData,function(res){

 						that.clearData();
 					},"Loc")
 				}
 			}
 		},
			// 查看库存
			viewInventory:function(){

				var that = this;

				var currentUrl = this.HOST + "RFInvQuery.do";

				var postData = {
					"Loc":this.Loc,
					"Sku":this.Sku,
					"PID":this.PID,
				}


				this.httpRequest(currentUrl,postData,function(res){
					
					that.$store.state.RFInvMoveCondition = {
						Loc:that.Loc,
						PID:that.PID,
						Sku:that.Sku,
						ToLoc:that.ToLoc,
						ToPID:that.ToPID,
						Qty:that.Qty,
					}
					
					// 判断是从库存查询页面跳转而来 还是从移动页面跳转而来
					that.$store.state.InventoryQuery = false;
					that.$store.state.RFInvMove = true;
					
						//跳转到库存详情页面
						that.$router.push({
							name:"InventoryDetails",
							query:{

							}
						})

					that.$store.state.QueryListData = res.data.data;
				},"Loc")
			},
			//返回上一页  首页
			toIndex:function(){
				this.$router.push({
					name:"Index",
				})
			}
		}
	}
</script>
<style scoped lang="less">
.RFInvMove{
	.container{
		padding-top: 1/50rem;
		&>div{
			width:100%;
			margin-top: 20/50rem;
			padding: 0 20/50rem;
			box-sizing: border-box;
			position: relative;
			overflow: hidden;

			span{
				margin-left: 5/50rem;
				text-align: right;
				width: 140/50rem;
				float: left;
			}
			&.prompt>span{
				float:none;
				padding-left: 155/50rem;
				color:#006400;
			}


			input{
				margin-left: 10/50rem;
				width:490/50rem;	
				height:55/50rem;
				padding:0 10/50rem;
			}
			i{
				position:absolute;
				right:80/50rem;
				font-size: 30/50rem;
				top: 15/50rem;
			}
		}
	}
}

// 374像素以下（包含374像素）
@media screen and (max-width: 374px){

	.RFInvMove{
		.container{
			&>div{
				i{
					top: 8/50rem;
					right:63/50rem;
					font-size: 37/50rem;
				}
				span{
					width: 180/50rem;
				}
				input{
					width:460/50rem;	
					height:50/50rem;
				}
				&.prompt>span{
					padding-left: 190/50rem;
				}
			}
		}
	}
}

// 255像素以下(包含255像素)
@media screen and (max-width: 255px){
	.RFInvMove{
		.container{
			&>div{
				height: 80/50rem;
				i{
					top: 13/50rem;
					right:63/50rem;
					font-size: 48/50rem;
				}
				span{
					width: 200/50rem;
				}
				input{
					width:440/50rem;	
					height:65/50rem;
				}
				&.prompt>span{
					padding-left: 10/50rem;
				}
			}
		}
	}
}
</style>


